.advantages {
    padding: 150px 0 328px;

    &__title {
        max-width: 607px;
    }

    &__list {
        display: flex;
        justify-content: space-between;
        margin: 54px 0 0;
    }

    &__item {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        padding: 35px 30px;
        min-height: 309px;
        max-width: 255px;
    }

    &__name {
        font-weight: 500;
        font-size: 24px;
        line-height: 32px;
        text-transform: uppercase;
        color: $mainColor;
    }

    &__parag {
        font-size: 14px;
        line-height: 26px;
    }

    @include media(950) {
        padding: 50px 0 100px;
        text-align: center;

        &__title {
            max-width: 100%;
        }

        &__list {
            flex-direction: column;
            align-items: center;
            margin: 20px 0 0;
        }

        &__item {
            padding: 20px;
            min-height: auto;
            max-width: 100%;
        }

        &__parag {
            margin: 10px 0 0;
        }

        &__btn {
            margin: 10px 0 0;
        }
    }
}