.brands {
    &__container {
        position: relative;
    }

    &__wrap {
        position: absolute;
        left: 30px;
        top: -103px;
        display: flex;
        border-radius: 2px;
        padding: 60px 0;
        width: 109%;
        box-shadow: 50px 50px 100px rgba(0, 0, 0, 0.14);
        background-color: #fff;
    }

    &__img {
        margin: 0 0 0 65px;
        width: 157px;
        height: 113px;
        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;

        &:nth-child(1) {
            background-image: url("../img/brands__img1.jpg");
        }

        &:nth-child(2) {
            background-image: url("../img/brands__img2.jpg");
        }

        &:nth-child(3) {
            background-image: url("../img/brands__img3.jpg");
        }

        &:nth-child(4) {
            background-image: url("../img/brands__img4.jpg");
        }

        &:nth-child(5) {
            background-image: url("../img/brands__img5.jpg");
        }
    }

    @include media(1170) {
        &__wrap {
            left: 0;
            flex-flow: row wrap;
            justify-content: space-between;
            padding: 30px;
            width: 100%;
        }

        &__img {
            margin: 0;
        }
    }

    @include media(950) {
        &__wrap {
            top: -50px;
            justify-content: space-evenly;
        }

        &__img {
            width: 80px;
            height: 40px;

            &:not(:first-child) {
                margin: 0 0 0 10px;
            }
        }
    }
}